/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from 'react'
import AssetAccordion from './AssetAccordion'
import { Row, Col, FormText } from 'reactstrap'
import { Edit, Trash } from 'react-feather'
import 'swiper/css/swiper.css'
import '../../assets/scss/plugins/extensions/swiper.scss'
import img1 from '../../assets/img/slider/banner-31.jpg'
import img2 from '../../assets/img/slider/banner-22.jpg'
import img3 from '../../assets/img/slider/banner-23.jpg'
import img4 from '../../assets/img/slider/banner-24.jpg'
import img5 from '../../assets/img/slider/banner-25.jpg'
import Swiper from 'react-id-swiper'
import 'swiper/css/swiper.css'
import { getData } from '../../redux/actions/data-list/'
import { connect } from 'react-redux'
const params = {
  effect: 'cube',
  grabCursor: true,
  autoplay: {
    delay: 2000,
    disableOnInteraction: true
  },
  cubeEffect: {
    shadow: true,
    slideShadows: true,
    shadowOffset: 20,
    shadowScale: 0.94
  },
  pagination: {
    el: '.swiper-pagination'
  }
}

const Portfolio = (props) => {
  const [assetShow, setassetShow] = useState(false)
  const [liabilityShow, setliabilityShow] = useState(false)
  const [assetList, setassetList] = useState([])
  const [liabilityList, setliabilityList] = useState([])

  useEffect(() => {
    props.getData()
  }, [])
  useEffect(() => {
    getAssets()
  }, [props.dataList])
  const getAssets = () => {
    const user = JSON.parse(localStorage.getItem('logInUserData'))
    assetList.splice(0, assetList.length)
    liabilityList.splice(0, liabilityList.length)
    user?.assets?.length &&
      user.assets.forEach((e, i) => {
        let nomin = ''
        e?.nominees?.length &&
          e.nominees.forEach((n, idx) => {
            if (idx === e.nominees.length - 1) {
              nomin = nomin + n.name
            } else {
              nomin = nomin + n.name + ', '
            }
          })
        const cont = (
          <div>
            <Row>
              {e?.assetDetails?.map((x, i) => {
                if (x.val !== '') {
                  return (
                    <Col md="5" sm="12" key={i + x.key}>
                      <FormText>
                        <b>{x.key} :</b> {assetShow ? x.val : '***********'}
                      </FormText>
                    </Col>
                  )
                }
                return ''
              })}
              <Col md="5" sm="12">
                <FormText>
                  <b>Nominees :</b> {assetShow ? nomin : '*******************'}
                </FormText>
              </Col>
              <Col md="2" sm="12">
                <FormText>
                  <b>
                    {' '}
                    <Edit
                      className="cursor-pointer mr-1"
                      size={window.screen.width < 500 ? 13 : 20}
                      onClick={() => {
                        console.log('event', e)
                      }}
                    />
                  </b>
                  <br />
                  <b>
                    <Trash
                      className="cursor-pointer"
                      size={window.screen.width < 500 ? 13 : 20}
                      onClick={() => {
                        console.log('event', e)
                      }}
                    />
                  </b>
                </FormText>
              </Col>
            </Row>
          </div>
        )
        const ast = {
          id: i + 1,
          title: e.assetType,
          content: cont
        }
        assetList.push(ast)
      })
    user?.liabilities?.length &&
      user.liabilities.forEach((e, i) => {
        let nomin = ''
        e.nominees.forEach((n, idx) => {
          if (idx === e.nominees.length - 1) {
            nomin = nomin + n.name
          } else {
            nomin = nomin + n.name + ', '
          }
        })
        const cont = (
          <div>
            <Row>
              {e.liabilityDetails.map((x, i) => {
                if (x.val !== '') {
                  return (
                    <Col md="6" sm="12" key={i + x.key}>
                      <FormText>
                        <b>{x.key} :</b> {liabilityShow ? x.val : '***********'}
                      </FormText>
                    </Col>
                  )
                } else return ''
              })}
              <Col md="6" sm="12">
                <FormText>
                  <b>Nominees :</b>{' '}
                  {liabilityShow ? nomin : '*******************'}
                </FormText>
              </Col>
              <Col md="6" sm="12">
                <FormText>
                  <b>Nominees :</b> {assetShow ? nomin : '*******************'}
                </FormText>
                <Col md="2" sm="12">
                  <FormText>
                    <b>
                      {' '}
                      <Edit
                        className="cursor-pointer mr-1"
                        size={window.screen.width < 500 ? 13 : 20}
                        onClick={() => {
                          console.log('event', e)
                        }}
                      />
                    </b>
                    <br />
                    <b>
                      <Trash
                        className="cursor-pointer"
                        size={window.screen.width < 500 ? 13 : 20}
                        onClick={() => {
                          console.log('event', e)
                        }}
                      />
                    </b>
                  </FormText>
                </Col>
              </Col>
            </Row>
          </div>
        )
        const ast = {
          id: i + 1,
          title: e.liabilityType,
          content: cont
        }
        liabilityList.push(ast)
      })
    setassetList(assetList?.reverse())
    setliabilityList(liabilityList?.reverse())
  }

  return (
    <React.Fragment>
      <h2 className="warning spacing nodisplay">Portfolio</h2>
      <Row>
        <Col lg="8" md="6" sm="12">
          <Row className="match-height">
            <Col md="12">
              <AssetAccordion
                heading="Assets List"
                collapseItems={assetList}
                show={() => {
                  setassetShow(!assetShow)
                  getAssets()
                }}
                assetShow={assetShow}
                page="portfolio"
              />
            </Col>
          </Row>
          <Row className="match-height">
            <Col md="12">
              <AssetAccordion
                heading="Liabilities List"
                collapseItems={liabilityList}
                show={() => {
                  setliabilityShow(!liabilityShow)
                  getAssets()
                }}
                assetShow={liabilityShow}
                page="portfolio"
              />
            </Col>
          </Row>
        </Col>
        <Col lg="4" md="6" sm="12">
          <div className="swipermobile">
            <Swiper {...params}>
              <div
                style={{
                  backgroundImage: `url(${img1})`,
                  backgroundSize: 'contain'
                }}
              ></div>
              <div
                style={{
                  backgroundImage: `url(${img2})`,
                  backgroundSize: 'contain'
                }}
              ></div>
              <div
                style={{
                  backgroundImage: `url(${img3})`,
                  backgroundSize: 'contain'
                }}
              ></div>
              <div
                style={{
                  backgroundImage: `url(${img4})`,
                  backgroundSize: 'contain'
                }}
              ></div>
              <div
                style={{
                  backgroundImage: `url(${img5})`,
                  backgroundSize: 'contain'
                }}
              ></div>
            </Swiper>
          </div>
        </Col>
      </Row>
    </React.Fragment>
  )
}
const mapStateToProps = (state) => {
  return {
    dataList: state.dataList,
    listLoading: state.dataList.totalRecordsLoading
  }
}
export default connect(mapStateToProps, { getData })(Portfolio)
